import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return {
            search: '',
            page: 1,
            haveMore: true,
            list: [],
            orderInfo: this.$store.state.pInfo,
            user: this.$store.state.userInfo,
        }
    },
    created(){
        this.pull(false)
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
    methods:{
        pull(h){
            if(this.haveMore){
                this.$post('PlatFormAPI/KnowledgeBase/GetDrHealthKnowledgeForPage',this.$pick({
                    DrId: this.user.Data.User.DoctorId,
                    OrgIds: this.user.Data.User.OrgId,
                    Key: this.search,
                    PageIndex: this.page,
                    PageSize: 15,
                })).then(o => {
                    if (!o.ReturnData || o.ReturnData.length < 15){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                    }
                })
            }
        },
        getMore(){
            const el = this.$refs.el
            let timer = null
            window.onscroll = () => {
                clearTimeout(timer)
                timer = setTimeout(() => {
                    const top = document.documentElement.scrollTop
                                || window.pageYOffset
                                || document.body.scrollTop,
                            h = el.scrollHeight,
                            wh = window.innerHeight

                    if (h - top - wh < 100 && this.haveMore){
                        this.page += 1
                        this.pull(true)
                    }
                }, 100)
            }
        },
        send(i){
            const pack = {
                "Title": i.ServicePackName,
                "Content":[
                    {
                        "Type":"Text",
                        "ContentElem":{
                            "Text": i.Title
                        }
                    }
                ],
                "ExtData":{
                    "Type":301,
                    "Data":{
                        "LinkURL": i.LinkURL
                    }
                },
                "IsSystem":false
            }
            this.$bridge.callhandler('Health', JSON.stringify(pack))
            this.$bridge.callhandler('Back')
        },
        searchFor(){
            this.haveMore = true
            this.page = 1
            this.pull(false)
        }
    }
}



